@font-face {
  font-family: 'icomoon';
  src: url('../../assets/fonts/icons/icomoon.eot?786z7c');
  src: url('../../assets/fonts/icons/icomoon.eot?786z7c#iefix') format('embedded-opentype'),
    url('../../assets/fonts/icons/icomoon.ttf?786z7c') format('truetype'),
    url('../../assets/fonts/icons/icomoon.woff?786z7c') format('woff'),
    url('../../assets/fonts/icons/icomoon.svg?786z7c#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class*="icon-"]:before,
.icon-extension {
  display: inline-block;
  font-family: 'icomoon' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

